import { makeStyles } from '@mui/styles';
import React from 'react';
const useStyles = makeStyles({
  root: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  day: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: '50%',
    width: '1.8rem',
    height: '1.8rem',
    color: 'silver',
    border: '0.1rem solid silver',
    lineHeight: 1,
    cursor: 'pointer',
  },
  daySelected: {
    background: 'orange',
    fontWeight: '700',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: '50%',
    width: '1.8rem',
    height: '1.8rem',
    color: 'white',
    border: '0.1rem solid orange',
    lineHeight: 1,
    cursor: 'pointer',
  },
});
function WeekDaySelector({ selected, onClick }) {
  const classes = useStyles();
  const weekArr = ['M', 'T', 'W', 'T', 'F', 'S', 'S'];
  return (
    <div className={classes.root}>
      {weekArr.map((day, index) => {
        return (
          <div
            className={!selected[index] ? classes.day : classes.daySelected}
            key={index}
            onClick={() => onClick(index)}
          >
            {day}
          </div>
        );
      })}
    </div>
  );
}

export default WeekDaySelector;
