import React, { useState } from "react";
import {
  Box,
  Button,
  Chip,
  Divider,
  Grid,
  IconButton,
  Modal,
  Typography,
  Paper,
  Card,
  CardContent,
  CardActions,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import WeekDaySelector from "./WeekDaySelector";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import { useNavigate } from "react-router-dom";
import { CurrencyRupee } from "@mui/icons-material";
import { apiCaller } from "../../services/apiCaller";
import { useDispatch } from "react-redux";
import { updateSnackbarState } from "../../reducer/commonSlice";
import { updateUserPosts } from "../../reducer/userSlice";

const useStyles = makeStyles((theme) => ({
  post: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    transition: "all 0.3s ease-in-out",
    "&:hover": {
      transform: "translateY(-5px)",
      boxShadow: theme.shadows[10],
    },
  },
  deleteModal: {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    borderRadius: theme.shape.borderRadius,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(4),
  },
  chip: {
    margin: theme.spacing(0.5),
  },
  priceChip: {
    backgroundColor: theme.palette.secondary.main,
    color: theme.palette.secondary.contrastText,
  },
}));

const Post = ({ postItem, isEditable, token }) => {
  const { title, weekDays, classes, teachingMode, price, author, _id, userId } =
    postItem;
  const { online, offline } = teachingMode[0];
  const styleClasses = useStyles();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);

  const handleEditPost = () => {
    navigate("/create", { state: postItem });
  };

  const handleDeletePost = () => {
    setDeleteModalOpen(false);
    apiCaller(`/gigs/${_id}`, "delete", {
      data: { userId },
      headers: { "x-access-token": token ?? "" },
    })
      .then((res) => {
        if (res.message === "Success") {
          dispatch(
            updateSnackbarState({
              open: true,
              message: "Post Deleted",
              type: "success",
            })
          );
          dispatch(updateUserPosts(_id));
        } else {
          throw new Error("Post Deletion Failed");
        }
      })
      .catch((err) => {
        dispatch(
          updateSnackbarState({
            open: true,
            message: "Post Deletion Failed",
            type: "error",
          })
        );
        console.error("Error", err);
      });
  };

  return (
    <Grid item xs={12} md={6} lg={4} justifyContent={"flex-start"}>
      <Card className={styleClasses.post}>
        <CardContent>
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mb={2}
          >
            <Typography variant="h6" component="div" overflow={"hidden"} noWrap>
              {title}
            </Typography>
            <Typography variant="body2" color="textSecondary">
              {author}
            </Typography>
          </Box>

          <Divider />

          <Box my={2}>
            <Typography variant="subtitle2" gutterBottom>
              Timing
            </Typography>
            <WeekDaySelector selected={weekDays} onClick={() => null} />
          </Box>

          <Box my={2}>
            <Typography variant="subtitle2" gutterBottom>
              Location
            </Typography>
            <Typography variant="body2" color="primary">
              {online && "Online"}
              {online && offline && " / "}
              {offline && "Offline"}
            </Typography>
          </Box>

          <Box my={2}>
            <Typography variant="subtitle2" gutterBottom>
              Classes
            </Typography>
            {classes && classes.length ? (
              classes.map((val, index) => (
                <Chip
                  key={index}
                  label={val}
                  size="small"
                  className={styleClasses.chip}
                />
              ))
            ) : (
              <Chip
                label="Not Specified"
                size="small"
                className={styleClasses.chip}
              />
            )}
          </Box>

          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
          >
            <Chip
              icon={<CurrencyRupee />}
              label={`${price}/hr`}
              className={styleClasses.priceChip}
            />
            <Button variant="contained" color="primary" size="small">
              Enquiry
            </Button>
          </Box>
        </CardContent>

        {isEditable && (
          <CardActions>
            <IconButton size="small" onClick={handleEditPost} aria-label="edit">
              <EditIcon htmlColor="#FE6B8B" />
            </IconButton>
            <IconButton
              size="small"
              onClick={() => setDeleteModalOpen(true)}
              aria-label="delete"
            >
              <DeleteIcon htmlColor="#FE6B8B" />
            </IconButton>
          </CardActions>
        )}
      </Card>

      <Modal
        open={deleteModalOpen}
        onClose={() => setDeleteModalOpen(false)}
        aria-labelledby="delete-post-modal"
      >
        <Paper className={styleClasses.deleteModal}>
          <Typography variant="h6" component="h2" gutterBottom>
            Delete Post
          </Typography>
          <Typography variant="body1" paragraph>
            You cannot undo this change. This post will be deleted forever.
          </Typography>
          <Box display="flex" justifyContent="flex-end">
            <Button onClick={() => setDeleteModalOpen(false)} color="primary">
              Cancel
            </Button>
            <Button
              onClick={handleDeletePost}
              color="secondary"
              variant="contained"
              sx={{ ml: 2 }}
            >
              Delete
            </Button>
          </Box>
        </Paper>
      </Modal>
    </Grid>
  );
};

export default Post;
