import { Container, Grid, Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';
import React, { useEffect, useState } from 'react';
import { fetchGigsData } from '../../reducer/gigsSlice';
import { useDispatch, useSelector } from 'react-redux';
import LoadingComponent from '../LoadingComponent';
import Post from '../Shared/post';
import { updateSearchPost } from '../../reducer/commonSlice';
import NoDataAvailable from '../Shared/NoDataAvailable';
const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexFlow: 'column',
    height: '100vh',
    width: '100vw',
    overflowY: 'scroll',
    paddingTop: '10vh',
    boxSizing: 'border-box',
  },
  scrollSection: {
    overflowY: 'scroll',
  },
  inputDesktop: {
    border: 'none',
    padding: '0.5rem 1rem',
    height: '3rem',
    width: 'inherit',
    boxSizing: 'border-box',
    margin: '1rem 0rem',
    fontSize: '1.2rem',
    borderLeft: '0.05rem solid silver',
    fontFamily: 'Rajdhani, sans-serif',
    '&:focus': {
      outline: 'none !important',
    },
  },
});

const Posts = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { loading, data } = useSelector((state) => state.gigsReducer);
  const { searchPost } = useSelector((state) => state?.commonReducer);

  const [searchValue, setSearchValue] = useState(searchPost);

  const filteredData = data.filter((post) =>
    post?.title?.toLowerCase()?.includes(searchValue.toLowerCase())
  );
  const handleSearchPost = (e) => {
    e.preventDefault();
    setSearchValue(e.target?.value);
    dispatch(updateSearchPost(e.target?.value));
  };

  useEffect(() => {
    dispatch(fetchGigsData());
  }, []);

  return (
    <div className={classes.root}>
      {loading ? (
        <LoadingComponent />
      ) : (
        <Container>
          <input
            type='text'
            className={classes.inputDesktop}
            id='exampleInputName'
            placeholder='Search Subject'
            onChange={handleSearchPost}
            value={searchValue}
          />
          <br />
          {searchPost && searchPost?.length ? (
            <>
              <Typography variant='h4' component={'span'}>
                Showing Results for:{' '}
              </Typography>
              <Typography variant='h4' component={'span'} color='orange'>
                {searchPost}
              </Typography>
            </>
          ) : null}
          <Grid container height={'100%'} justifyContent='flex-start' gap={"2rem"}>
            {filteredData?.length ? (
              filteredData.map((postItem, index) => {
                return (
                  <Post
                    postItem={{
                      ...postItem,
                      selected: [true, false, true, false, false, true, false],
                    }}
                    key={index}
                  />
                );
              })
            ) : (
              <NoDataAvailable message={'No Post Found'} />
            )}
          </Grid>
        </Container>
      )}
    </div>
  );
};

export default Posts;
